import { Injectable } from '@angular/core';
import { MapperInterface } from '@telenet/ng-lib-shared';
import { Prices } from '../../interfaces/prices.interface';
import { PricesDto } from '../../interfaces/dto/prices-dto.interface';

@Injectable({
  providedIn: 'root',
})
export class PricesMapper implements MapperInterface<PricesDto, Prices> {
  toModel(rawPrices: PricesDto): Prices {
    return this.createPrices(rawPrices);
  }

  private createPrices(rawPrices: PricesDto): Prices {
    if (rawPrices && rawPrices.taxInclPrices) {
      const taxtIncl = {
        currency: rawPrices.taxInclPrices.currency,
        oneTimeTotal: rawPrices.taxInclPrices.oneTimeTotal,
        oneTimeDiscount: rawPrices.taxInclPrices.oneTimeDiscount,
        oneTimeDiscounted: rawPrices.taxInclPrices.oneTimeDiscounted,
        oneTimeDiscountedOnNextInvoice: rawPrices.taxInclPrices.oneTimeDiscountedOnNextInvoice,
        recurrentTotal: rawPrices.taxInclPrices.recurrentTotal,
        recurrentDiscount: rawPrices.taxInclPrices.recurrentDiscount,
        recurrentDiscounted: rawPrices.taxInclPrices.recurrentDiscounted,
        upfrontTotal: rawPrices.taxInclPrices.upfrontTotal,
        discountPrice: rawPrices.taxInclPrices.discountPrice,
        yupUpfrontPayment: {
          recurrentDiscounted: rawPrices.taxInclPrices.yupUpfrontPayment?.recurrentDiscounted,
          totalDiscounted: rawPrices.taxInclPrices.yupUpfrontPayment?.totalDiscounted,
        },
      };

      const taxEcl = {
        currency: rawPrices.taxExclPrices.currency,
        oneTimeTotal: rawPrices.taxExclPrices.oneTimeTotal,
        oneTimeDiscount: rawPrices.taxExclPrices.oneTimeDiscount,
        oneTimeDiscounted: rawPrices.taxExclPrices.oneTimeDiscounted,
        oneTimeDiscountedOnNextInvoice: rawPrices.taxExclPrices.oneTimeDiscountedOnNextInvoice,
        recurrentTotal: rawPrices.taxExclPrices.recurrentTotal,
        recurrentDiscount: rawPrices.taxExclPrices.recurrentDiscount,
        recurrentDiscounted: rawPrices.taxExclPrices.recurrentDiscounted,
        upfrontTotal: rawPrices.taxExclPrices.upfrontTotal,
        discountPrice: rawPrices.taxExclPrices.discountPrice,
        yupUpfrontPayment: {
          recurrentDiscounted: rawPrices.taxExclPrices.yupUpfrontPayment?.recurrentDiscounted,
          totalDiscounted: rawPrices.taxExclPrices.yupUpfrontPayment?.totalDiscounted,
        },
      };
      return {
        taxInclPrices: taxtIncl,
        taxExclPrices: taxEcl,
      };
    }
  }
}
